'use client';

import { useState } from 'react';
import { cn } from '@/lib/utils';

export function BudgetOverview() {
    const [budgets] = useState([
        { id: '1', name: 'Q1 Feed & Supplies', allocated: 50000, spent: 34200, category: 'OPERATIONAL' },
        { id: '2', name: 'Machine Maintenance', allocated: 12000, spent: 11500, category: 'MAINTENANCE' },
        { id: '3', name: 'New Irrigation Project', allocated: 80000, spent: 12000, category: 'CAPITAL' },
    ]);

    return (
        <div className="p-4 space-y-6">
            <h1 className="text-2xl font-bold">Budgets</h1>

            <div className="grid gap-4">
                {budgets.map((budget) => {
                    const percent = (budget.spent / budget.allocated) * 100;
                    return (
                        <div key={budget.id} className="bg-card border rounded-2xl p-4 shadow-sm space-y-3">
                            <div className="flex justify-between items-start">
                                <div>
                                    <h3 className="font-bold">{budget.name}</h3>
                                    <span className="text-[10px] uppercase font-bold text-muted-foreground">{budget.category}</span>
                                </div>
                                <div className="text-right">
                                    <p className="text-lg font-bold">${budget.spent.toLocaleString()}</p>
                                    <p className="text-[10px] text-muted-foreground uppercase font-bold">of ${budget.allocated.toLocaleString()}</p>
                                </div>
                            </div>

                            <div className="h-2 w-full bg-accent rounded-full overflow-hidden">
                                <div
                                    className={cn(
                                        "h-full transition-all duration-1000",
                                        percent > 90 ? "bg-destructive" : percent > 70 ? "bg-amber-500" : "bg-primary"
                                    )}
                                    style={{ width: `${Math.min(percent, 100)}%` }}
                                />
                            </div>
                        </div>
                    );
                })}
            </div>
        </div>
    );
}
