'use client';

import { useState } from 'react';
import { cn } from '@/lib/utils';
import { CheckIcon, XMarkIcon, CurrencyDollarIcon } from '@heroicons/react/24/outline';
import { db } from '@/lib/offline/db';

export function ApprovalInbox() {
    const [requests, setRequests] = useState([
        { id: '1', requester: 'John Doe', amount: 1200, category: 'FEED', description: 'Emergency cattle feed for North Lot', status: 'PENDING' },
        { id: '2', requester: 'Jane Smith', amount: 450, category: 'TOOLS', description: 'Replacement irrigation valves (10x)', status: 'PENDING' },
    ]);

    const handleDecision = async (id: string, decision: 'APPROVED' | 'REJECTED') => {
        // In a real app, this would use the finance-client to update local DB and queue sync
        setRequests(prev => prev.filter(r => r.id !== id));

        await db.outbox_jobs.add({
            action: 'SPEND_REQUEST_DECISION',
            data: { id, decision },
            priority: 3,
            attempts: 0,
            nextAttemptAt: Date.now(),
            farmId: 'farm-123',
            status: 'PENDING',
        });
    };

    return (
        <div className="p-4 space-y-6">
            <h1 className="text-2xl font-bold">Pending Approvals</h1>

            <div className="grid gap-4">
                {requests.length === 0 ? (
                    <div className="text-center py-12 border-2 border-dashed rounded-2xl">
                        <p className="text-muted-foreground">All caught up! No pending requests.</p>
                    </div>
                ) : (
                    requests.map((req) => (
                        <div key={req.id} className="bg-card border rounded-2xl p-5 shadow-sm space-y-4">
                            <div className="flex justify-between items-start">
                                <div className="flex gap-3">
                                    <div className="h-10 w-10 rounded-full bg-accent flex items-center justify-center">
                                        <CurrencyDollarIcon className="h-6 w-6 text-primary" />
                                    </div>
                                    <div>
                                        <h3 className="font-bold">{req.requester}</h3>
                                        <p className="text-[10px] uppercase font-bold text-muted-foreground">{req.category}</p>
                                    </div>
                                </div>
                                <div className="text-right">
                                    <p className="text-xl font-black text-primary">${req.amount}</p>
                                </div>
                            </div>

                            <p className="text-sm text-muted-foreground">{req.description}</p>

                            <div className="flex gap-3">
                                <button
                                    onClick={() => handleDecision(req.id, 'APPROVED')}
                                    className="flex-1 py-3 bg-green-600 text-white rounded-xl font-bold flex items-center justify-center gap-2"
                                >
                                    <CheckIcon className="h-5 w-5" />
                                    Approve
                                </button>
                                <button
                                    onClick={() => handleDecision(req.id, 'REJECTED')}
                                    className="flex-1 py-3 bg-accent text-accent-foreground rounded-xl font-bold flex items-center justify-center gap-2"
                                >
                                    <XMarkIcon className="h-5 w-5" />
                                    Decline
                                </button>
                            </div>
                        </div>
                    ))
                )}
            </div>
        </div>
    );
}
